<!--
 * @Descripttion: 指引单样式组件
 * @Author: 曾元球
 * @Date: 2019-06-24 18:37:53
 * @LastEditors: 曾元球
 * @LastEditTime: 2019-07-31 13:42:21
 * @param: {Object} [user]
 * @param: {Array} [guideSheetData]
 * @example: <guide-sheet-html :user='{Object}' :guideSheetData='{Array}' />
 -->

<template>
  <div class="guide-sheet">
    <div class="head-barcode flex-center">
      <div class="flex-v-center">
        <div class="logo flex-center flex-v-center"
          v-show="user.createOrganizationIdLogo"
          :style="{background:user.createOrganizationIdLogo?'#fff':'#eee'}">
          <img :src="user.createOrganizationIdLogo"
            alt="LOGO">
        </div>
        <div class="barcode-box">
          <div class="title">{{user.createOrganizationName}}</div>
          <svg id="barcode"></svg>
          <!-- <div class="barcode-text flex flex-justify"> -->
          <!-- <span v-for="(item, i) in barcode" :key="i">{{item}}</span> -->
          <!-- </div> -->
        </div>
      </div>
    </div>
    <div class="userinfo flex-v-center">
      <div class="avatar flex-center flex-v-center"
        :style="{background:user.addOn1?'#fff':'#eee'}">
        <img v-if="user.addOn1"
          :src="user.addOn1"
          alt="图像" />
        <template v-else>
          <img src="@/assets/img/person-woman.png"
            v-if="user.sexType==2"
            alt="图像" />
          <img src="@/assets/img/person-man.png"
            v-else
            alt="图像" />
        </template>
      </div>
      <div class="info flex-item">
        <div class="inline-box"
          style="width:40%">
          <label>姓名：</label>
          <span>{{user.userName}}</span>
        </div>
        <div class="inline-box"
          style="width:20%">
          <label>性别：</label>
          <span>{{user.sexType == 1 ? '男' : user.sexType == 2 ? '女' : '不详'}}</span>
        </div>
        <div class="inline-box"
          style="width:20%">
          <label>年龄：</label>
          <span>{{user.age}}</span>
        </div>
        <div class="inline-box"
          style="width:20%">
          <label>体检次数：</label>
          <span>{{user.examNum}}</span>
        </div>

        <div class="inline-box"
          style="width:33%">
          <label>民族：</label>
          <span>{{user.nationCase | emptyData}}</span>
        </div>
        <div class="inline-box"
          style="width:34%">
          <label>开单人员：</label>
          <span>{{user.createUserName}}</span>
        </div>
        <div class="inline-box"
          style="width:33%">
          <label>开单时间：</label>
          <span>{{user.createDate | formatDate}}</span>
        </div>
        <div class="inline-box"
          style="width:50%">
          <label>联系电话：</label>
          <span>{{user.contactPhone}}</span>
        </div>
        <div class="inline-box"
          style="width:50%">
          <label>身份证号：</label>
          <span>{{user.idNo}}</span>
        </div>
        <div class="inline-box"
          style="min-width:50%">
          <label>现住址：</label>
          <span>{{user.liveAddress}}</span>
        </div>
        <!-- <div class="inline-box" style="min-width:50%">
          <label>体检套餐：</label>
          <span>教学楼示标档吉林省地村要</span>
        </div> -->
      </div>
    </div>
    <div class="content">
      <div class="title-item">温馨提示</div>
      <ol class="ol-list">
        <li v-for="text in headWaring"
          :key="text">{{text}}</li>
      </ol>
    </div>
    <div class="content">

      <!-- 按科室显示大项 -->
      <!-- <div v-for="(item, i) in guideSheetData"
        :key="i">
        <div class="title-item">{{item.dname}}</div>
        <dl class="box-item">
          <dt class="flex">
            <div class="flex-item"
              style="padding-left:30px">项目</div>
            <div class="autograph">检查医生</div>
            <div class="flex-item">体检地点</div>
          </dt>
          <dd class="flex"
            v-for='(items, ii) in item.projectList'
            :key="ii">
            <div class="flex-item flex-v-center">
              <span class="box-line"></span>
              <span class="span-text">{{items.pname}}</span>
            </div>
            <div class="autograph">
              <div style="height:100%;border-bottom: 1px solid #B2B2B2"></div>
            </div>
            <div class="flex-item"></div>
          </dd>
        </dl>
      </div> -->
      <dl class="box-item">
        <dt class="flex">
          <div class="flex-item"
            style="padding-left:30px">项目</div>
          <div class="autograph">检查医生</div>
          <div class="flex-item">体检地点</div>
        </dt>
        <dd class="flex"
          v-for='(items, ii) in guideSheetData'
          :key="ii">
          <div class="flex-item flex-v-center">
            <span class="box-line"></span>
            <span class="span-text">{{items.pname}}</span>
          </div>
          <div class="autograph">
            <div style="height:100%;border-bottom: 1px solid #B2B2B2"></div>
          </div>
          <div class="flex-item"></div>
        </dd>
      </dl>
      <ol class="ol-list">
        <li v-for="text in endWaring"
          :key="text">{{text}}</li>
      </ol>
    </div>
    <div class="footer">
      欢迎并感谢您前来体检，祝您身体健康！
    </div>
  </div>
</template>

<script>
import jsBarcode from 'jsbarcode'
export default {
  name: 'GuideSheetHtml',
  props: {
    user: {
      type: Object,
      default() {
        return {}
      }
    },

    guideSheetData: {
      type: Array,
      default() {
        return []
      }
    }
  },

  mounted() {
    console.log(this.guideSheetData)
  },
  computed: {
    endWaring() {
      let s = this.user.endWaring
      return s ? s.split('@@') : [] // 数据库固定用 @@ 标识分隔
    },
    headWaring() {
      let s = this.user.headWaring
      return s ? s.split('@@') : []
    }
  },

  methods: {
    createBarcode(val, width, height) {
      jsBarcode('#barcode', val, { width, height })
    }
  },

  watch: {
    'user.examUserId'(val) {
      if (!val) return
      this.$nextTick(() => {
        this.createBarcode(val, 2, 36)
      })
    }
  }
}
</script>

<style lang="less" scoped>
.guide-sheet {
  margin: 0 auto;
  max-width: 100%;
  width: 800px;
  border: 1px solid #b2b2b2;
}
.head-barcode {
  padding: 16px 0;
  border-bottom: 1px solid #b2b2b2;
}
.logo {
  margin-right: 24px;
  width: 80px;
  height: 80px;
  background: #eee;
  overflow: hidden;
  img {
    width: 100%;
  }
}
.title {
  margin: 0 auto;
  width: 280px;
  line-height: 1;
  text-align: center;
  font-size: 30px;
  font-weight: bold;
  font-family: '楷体';
  white-space: normal;
  color: #000;
}
#barcode {
  display: block;
  margin: 0 auto;
}
// .barcode-text {
//   padding: 0 10px;
//   line-height: 1;
//   font-size: 20px;
//   font-family: '楷体'
// }

.userinfo {
  padding: 12px;
  border-bottom: 1px dashed #b2b2b2;
}
.avatar {
  margin-right: 12px;
  width: 75px;
  height: 90px;
  background: #eee;
  overflow: hidden;
  img {
    width: 100%;
  }
}
.info {
  font-size: 0;
  & > .inline-box {
    display: inline-block;
    padding: 0 20px 5px 0;
    box-sizing: border-box;
    font-size: 14px;
  }
}
.content {
  padding: 12px;
  border-bottom: 1px dashed #b2b2b2;
}
.title-item {
  font-size: 16px;
  font-weight: 700;
}
.box-item {
  margin: 12px 0;
  border: 1px solid #b2b2b2;
  text-align: center;
  dt {
    background: #f3f3f3;
  }
  dd {
    border-top: 1px solid #b2b2b2;
  }
  .autograph {
    .flex-width(200px);
  }
  .flex-item,
  .autograph {
    padding: 12px;
  }
  // dt, dd {
  //   & > .flex-item:first-child {
  //     padding-left: 20px;
  //     text-align: left;
  //   }
  // }
}
.box-line {
  display: inline-block;
  vertical-align: top;
  margin-right: 10px;
  .flex-width(16px);
  height: 16px;
  border: 1px solid #b2b2b2;
}
.span-text {
  flex: 1;
  white-space: wrap;
}
.ol-list {
  padding: 20px 0 20px 120px;
  list-style-type: decimal;
}
.tip {
  font-weight: 700;
  color: red;
  border-bottom: 1px solid;
}
.footer {
  height: 44px;
  line-height: 44px;
  text-align: center;
}
</style>


