<!--
 * @Descripttion: 登记台人员详情信息
 * @Author: 曾元球
 * @Date: 2019-07-01 09:28:15
 * @LastEditors: 曾元球
 * @LastEditTime: 2019-08-02 09:26:58
 -->
<template>
  <div class="page flex-v">
    <div class="head flex-v-center">
      <el-breadcrumb separator-class="el-icon-arrow-right"
        class="my-breadcrumb">
        <el-breadcrumb-item :to="{name:'CheckIn'}">登记台</el-breadcrumb-item>
        <el-breadcrumb-item>体检详情</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container flex-item">
      <div class="top">
        <div class="top-head">
          <i class="iconfont bj-person"></i>&nbsp;&nbsp;体检人员
        </div>
        <div class="top-content flex">
          <div class="avatar-box">
            <div class="avatar flex-center flex-v-center">
              <img v-if="userData.addOn1"
                :src="userData.addOn1" />
              <template v-else>
                <img src="@/assets/img/person-woman.png"
                  alt
                  v-if="userData.sexType==2" />
                <img src="@/assets/img/person-man.png"
                  alt
                  v-else />
              </template>
            </div>
          </div>

          <el-form size="small"
            label-position="left"
            class="form-box">
            <el-row>
              <el-col :span="6">
                <el-form-item label="体检流水号：">
                  <span class="text">{{userData.sysGuid}}</span>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="姓名：">
                  <span class="text">{{userData.userName}}</span>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="性别：">
                  <span class="text">
                    {{
                    userData.sexType == 1 ? '男' :
                    userData.sexType == 2 ? '女' : '不详'
                    }}
                  </span>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="出生日期：">
                  <span class="text">{{userData.birthday | formatDate}}</span>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="体检次数：">
                  <span class="text">{{examNum}}</span>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="年龄：">
                  <span class="text">{{userData.age}}</span>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="民族：">
                  <span class="text">{{userData.nationCase}}</span>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="身份证号：">
                  <span class="text">{{userData.idNo}}</span>
                </el-form-item>
              </el-col>

              <el-col :span="6">
                <el-form-item label="体检日期：">
                  <span class="text">{{userData.examDate | formatDate}}</span>
                </el-form-item>
              </el-col>
              <el-col :span="18">
                <el-form-item label="户籍地址：">
                  <span class="text">{{userData.residenceAddress}}</span>
                </el-form-item>
              </el-col>

              <el-col :span="6">
                <el-form-item label="操作医生：">
                  <span class="text">{{userData.createUserName}}</span>
                </el-form-item>
              </el-col>
              <el-col :span="18">
                <el-form-item label="现住址：">
                  <span class="text">{{userData.liveAddress}}</span>
                </el-form-item>
              </el-col>

              <el-col :span="6">
                <el-form-item label="联系电话：">
                  <span class="text">{{userData.contactPhone}}</span>
                </el-form-item>
              </el-col>
              <el-col :span="18">
                <el-form-item label="医疗机构：">
                  <span class="text">{{userData.createOrganizationName}}</span>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
        </div>
      </div>
      <div class="bottom">
        <div class="body">
          <div class="down-box">
            <h4 class="flex">
              <div class="icon-box">
                <div class="icon-block">

                </div>
              </div>包含项目
            </h4>
            <div class="item-box flex-inline">
              <dl>
                <dt class="flex">
                  <div class="side-left">项目名称</div>
                  <div class="content-right flex-item">小项名称</div>
                </dt>
                <dd class="flex"
                  v-for="(item,index) in list"
                  :key="index">
                  <div class="side-left">{{item.pname}}</div>
                  <div class="content-right flex-item">{{item.smallName}}</div>
                </dd>
              </dl>
            </div>
          </div>
        </div>
      </div>
      <footer class="btn-box">
        <el-button class="btn-info"
          @click="edit"
          v-if="userData.examStatus==1">修改</el-button>
        <el-button class="btn-red"
          @click="beforePrint"
          :loading="printLoading">打印指引单</el-button>
      </footer>
    </div>
    <div ref='print-html-box'
      style="position:absolute;top:0;left:-8000px;">
      <guide-sheet-html class="guide-sheet"
        :user='user'
        :guideSheetData='guideSheetData'></guide-sheet-html>
    </div>

    <el-dialog :visible.sync='isShowDialog'
      title="指引单"
      class="flex"
      width="1120px"
      center>
      <guide-sheet-html class="guide-sheet"
        :user='user'
        :guideSheetData='guideSheetData'></guide-sheet-html>
      <div slot="footer">
        <el-button class='btn-hollowing'
          @click="isShowDialog=false">取消</el-button>
        <el-button class='btn-info'
          @click="printGuide">打印</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import printMethod from '@/lib/print-method'
import GuideSheetHtml from '@/components/GuideSheetHtml'
import { formatDate } from '@/lib/utils'
export default {
  name: 'CheckInDetail',
  components: { GuideSheetHtml },
  data() {
    return {
      tjsysGuid: '',
      sysGuid: '',
      bigProject: [], //大项
      smallProject: [], //小项
      userData: {
        sysGuid: ''
      },
      smallprojectData: [],
      guideSheetData: [],
      user: {},
      examNum: '',
      printLoading: false,
      isShowDialog: false
    }
  },
  created() {
    this.tjsysGuid = this.$route.query.tjsysGuid
    this.sysGuid = this.$route.query.sysGuid
    this.getData(this.tjsysGuid)
    // this.getGuideSheet(this.tjsysGuid)
  },
  computed: {
    list() {
      return this.bigProject.map(item => {
        let o = { pname: item.pname, smallName: [] }
        this.smallProject.forEach(items => {
          if (item.sysGuid === items.parentId) o.smallName.push(items.pname)
        })
        o.smallName = o.smallName.join('、')
        return o
      })
    }
  },
  methods: {
    getData(sysGuid) {
      let url = '/sys/project/getShowExam'
      this.$post(url, { sysGuid })
        .then(({ data }) => {
          this.getExamNum(data.user.idNo)
          this.bigProject = data.bigProject
          this.smallProject = data.smallProject
          this.userData = data.user
        })
        .catch(error => {
          error && this.$message.error(error)
        })
    },

    getExamNum(idNo) {
      this.$post('/user/examUser/getExamTimes', {
        idNo,
        type: 'total'
      })
        .then(({ data }) => {
          this.examNum = data.split(',')[0]
        })
        .catch(err => err)
    },

    // 获取指引单数据
    getGuideSheet(examUserId) {
      this.$post('/user/examUser/showReportGuide', {
        examUserId
      }).then(({ data }) => {
        let { deptList, projectList, user } = data
        this.user = user
        this.guideSheetData = deptList.map(item => {
          let aTemp = projectList.filter(t => t.deptId === item.sysGuid)
          return { ...item, projectList: aTemp }
        })
        console.log(this.guideSheetData)
      })
    },

    edit() {
      this.$router.push({
        name: 'CheckInPerson',
        query: {
          tjsysGuid: this.tjsysGuid,
          sysGuid: this.sysGuid
        }
      })
    },

    printGuide() {
      return printMethod(this.$refs['print-html-box'], '指引单')
    },

    beforePrint() {
      this.printLoading = true
      function handleFn() {
        if (~navigator.userAgent.indexOf('Chrome')) {
          setTimeout(() => {
            this.printGuide().then(() => {
              this.printLoading = false
            })
          }, 300)
          return
        }
        this.isShowDialog = true
        this.printLoading = false
      }
      if (this.guideSheetData.length) return handleFn.call(this)

      this.$post('/user/examUser/showReportGuide', {
        examUserId: this.tjsysGuid
      })
        .then(({ data }) => {
          let { projectList, user } = data
          this.user = user
          this.guideSheetData = projectList
          handleFn.call(this)
        })
        .catch(err => {
          this.printLoading = false
          this.$message.error(err)
        })
    }
  }
}
</script>

<style lang="less" scoped>
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid @default-border;
}
.container {
  padding: 30px 20px;
  overflow-y: auto;
}
.top-content {
  padding-top: 25px;
}
.avatar-box {
  margin-right: 38px;
  width: 150px;
  p {
    padding: 16px 0 6px;
    font-size: 12px;
    color: #999;
  }
}
.avatar {
  height: 180px;
  width: 150px;
  overflow: hidden;
  background: #ccc;
  img {
    width: 100%;
  }
}
.el-form.form-box {
  max-width: 1280px;
  /deep/ .el-form-item__label {
    padding-right: 5px;
    color: #333;
  }
  /deep/ .el-form-item--small.el-form-item {
    margin-bottom: 8px;
  }
}
.bottom {
  margin-top: 40px;
  h4.flex {
    align-items: flex-end;
    // line-height: 1;
    // font-size: 14px;
  }
  .body {
    margin-top: 30px;
    // width: 620px;
  }
  .icon-box {
    position: relative;
    padding-top: 5px;
    width: 36px;
    height: 24px;
    &:after {
      content: '';
      position: absolute;
      top: 6px;
      left: 8px;
      width: 2px;
      height: 16px;
      background: #2979ff;
    }
    .icon-block {
      position: relative;
      padding-top: 6px;
      width: 27px;
      height: 100%;
      border-radius: 0 0 0 5px;
      background: #2979ff;
      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: -5px;
        width: 15px;
        height: 5px;
        background: #bbdefb;
      }
      &:after {
        content: '';
        display: block;
        margin: 0 auto;
        width: 16px;
        height: 3px;
        border-width: 2px 0;
        border-style: solid;
        border-color: #fff;
      }
    }
  }
  .item-box {
    margin: 0 0 0 36px;
  }
  .item {
    float: left;
    margin: 20px 30px 0 0;
    padding: 0 16px;
    line-height: 32px;
    border-radius: 4px;
    background: @color-green;
    color: #fff;
  }
}
.down-box {
  margin-top: 60px;
  .item-box {
    padding-top: 20px;
  }
}
dl {
  min-width: 580px;
  text-align: center;
}
dt {
  border-radius: 2px;
  background: #eaf8f4;
}
dt,
dd {
  padding: 15px 0;
}
.side-left {
  .flex-width(160px);
}
.content-right {
  padding: 0 50px;
}

.top-head,
.bottom-head {
  color: #111;
  .iconfont {
    font-size: 24px;
  }
  .bj-person {
    color: #0ed7b2;
  }
  .bj-menu {
    color: @color-red;
  }
}

footer.btn-box {
  display: block;
  padding: 60px 0 30px;
  text-align: center;
}

// 可能会用到公共样式
.text {
  color: #666;
}
</style>


